/* CUSTOM STYLES */
/* RESET */
*, *:before, *:after { @apply box-border !outline-none; }
html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video { @apply m-0 p-0 border-none text-[100%] [font:inherit] align-baseline [text-rendering:optimizeLegibility] [-webkit-font-smoothing:antialiased] [text-size-adjust:none]; }
body { @apply text-[100%] leading-normal font-sans select-none bg-bodyBackgroundColor; }
input, form, table, select, textarea, button { @apply font-sans text-[100%] leading-normal rounded-none !appearance-none resize-none outline-none border-none bg-transparent; }
select::-ms-expand { @apply !hidden; }
*, *:focus, *:focus-visible, *:hover, *:active, *::before, *::after { @apply !outline-none; }
footer, header, nav, section, main, article { @apply block; }
a, a:hover, a:active, a:focus, a:visited { @apply decoration-0; }
ol, ul, menu { @apply list-none m-0 p-0; }    
blockquote, q { @apply [quotes:none]; }
blockquote:before, blockquote:after, q:before, q:after { @apply content-none; }
table { @apply border-spacing-0 border-collapse; }
input { @apply appearance-none; }
svg { @apply overflow-visible block; }
input[type=search]::-ms-clear { @apply hidden h-0 w-0; }
input[type=search]::-ms-reveal { @apply hidden h-0 w-0; }
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { @apply hidden; }
strong {@apply !font-bold;}

/* TABLE FIX */
.table-wrapper {@apply !overflow-x-auto !overflow-y-hidden;}
.table-wrapper {@apply scrollbar scrollbar-h-[5px] scrollbar-track-rounded-[5px] scrollbar-thumb-rounded-[5px] scrollbar-thumb-primary-500 scrollbar-track-primary-500/10;}
.table-wrapper table {@apply !m-0;}
.table-wrapper table {@apply text-[#111111] w-full min-w-[768px] border-spacing-0 border-collapse;}
.table-wrapper table tr {@apply border border-solid border-[#111111]/50;}
.table-wrapper table thead {@apply border border-solid border-[#111111]/50;}
.table-wrapper table tr td {@apply border border-solid border-[#111111]/50 py-[10px] px-[50px];}
.table-wrapper table thead th {@apply border border-solid border-[#111111]/50 py-[10px] px-[50px] font-bold bg-primary-500 text-white;}

/* FORM | SELECT FIX */
select:required:invalid { @apply text-black/50; }
option[value=""][disabled] { @apply hidden; }
option {@apply text-black;}

/* TEXT EDITOR */
.text-editor { 
  @apply editor
  [&_*]:!mt-0 [&_*]:!mb-0 sm:[&_br]:hidden flex flex-col gap-[2rem] xl:gap-[1.75rem] lg:gap-[1.5rem] md:gap-[1.25rem] sm:gap-[1rem]
  [&_ul]:flex [&_ul]:flex-col [&_ul]:gap-[2rem] xl:[&_ul]:gap-[1.75rem] lg:[&_ul]:gap-[1.5rem] md:[&_ul]:gap-[1.25rem] sm:[&_ul]:gap-[1rem]

  editor-em:italic
  editor-strong:font-bold editor-strong:[color:inherit]
  editor-img:h-auto editor-img:max-w-full
  editor-hr:w-full editor-hr:h-[1px] editor-hr:bg-primary-500 editor-hr:border-0

  editor-headings:text-[#000000] editor-headings:leading-normal editor-headings:font-normal
  editor-h1:text-[64px] editor-h1:leading-[80px] editor-h1:font-semibold xl:editor-h1:text-[42px] lg:editor-h1:text-[36px] md:editor-h1:text-[30px]
  editor-h2:text-[48px] editor-h2:leading-[64px] editor-h2:font-semibold xl:editor-h2:text-[36px] lg:editor-h2:text-[30px] md:editor-h2:text-[24px]
  editor-h3:text-[32px] editor-h3:leading-[48px] editor-h3:font-semibold xl:editor-h3:text-[30px] lg:editor-h3:text-[24px] md:editor-h3:text-[20px] 
  editor-h4:text-[24px] editor-h4:leading-[36px] editor-h4:font-semibold xl:editor-h4:text-[24px] lg:editor-h4:text-[20px] md:editor-h4:text-[18px]
  editor-h5:text-[20px] editor-h5:leading-[30px] editor-h5:font-semibold xl:editor-h5:text-[20px] lg:editor-h5:text-[18px] md:editor-h5:text-[16px]
  editor-h6:text-[20px] editor-h6:leading-[28px] editor-h6:font-semibold xl:editor-h6:text-[18px] lg:editor-h6:text-[16px] md:editor-h6:text-[14px]
  editor-ul:list-disc marker:w-[10px] marker:h-[10px] marker:text-primary-500 
  editor-li:text-[#000000] editor-li:leading-normal editor-li:text-[18px] lg:editor-li:text-[16px] sm:editor-li:text-[14px]
  editor-p:text-[#000000] editor-p:leading-[28px] editor-p:text-[18px] lg:editor-p:text-[16px] sm:editor-p:text-[14px]
}

/* MAIN BASED */
body {@apply scrollbar scrollbar-w-[5px] scrollbar-track-rounded-[5px] scrollbar-thumb-rounded-[5px] scrollbar-thumb-primary-500 scrollbar-track-primary-500/10}
h1, h2, h3, h4, h5, h6 { @apply lg:leading-normal; }

.is-close-btn {
  right: 45px !important;
  top: 30px !important;
  color: #000;
}
.is-close-btn:hover {
 rotate: 180deg;
}

.dynamic-sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 20px;
    -webkit-transition: all .45s ease !important;
    -o-transition: all .45s ease !important;
    -moz-transition: all .45s ease !important;
    transition: all .45s ease !important
}